<style lang="scss">
#app-header {
  .open-notifications:hover {
    .icon-notifications {
      transition: 200ms ease all;
      animation: swing 3s;
      animation-timing-function: ease-out;
    }
  }

  @keyframes swing {
    0% {
      transform: rotate(0);
    }

    10% {
      transform: rotate(30deg);
    }

    20% {
      transform: rotate(-30deg);
    }

    30% {
      transform: rotate(20deg);
    }

    40% {
      transform: rotate(-10deg);
    }

    50% {
      transform: rotate(0);
    }
  }
}
</style>
